import ValidationEngine from '../validation-engine/validation-engine'
import { mapGetters } from 'vuex'
import constants from './dynamic-form-constants'
import {Autocomplete} from 'vuejs-auto-complete'
import {result} from "lodash-es";


const dynamicFormMixin = {
  props: ["id", "reValidate" ,"orderDetails"],
  data () {
    return {
      auto_suggest_display : ''
    }
  },
  async created() {
   await this.$store.dispatch('dynamic_form/removeGroupSuggestion')
  },
  components: { Autocomplete },
  watch: {
    reValidate: async function () {
      if (this.reValidate) {
        await this.validate();
      }
    }
  },
 
  mounted() {
    const field = {...this.currentField};
    let attribute_val =''
    if(this.orderDetails && this.orderDetails["recurrence_info"] && this.orderDetails["recurrence_info"]["master_order"] &&
      this.orderDetails["recurrence_info"]["master_order"]["order_values"] && 
      this.orderDetails["recurrence_info"]["master_order"]["order_values"]["entered_inputs"] &&  
      this.orderDetails["recurrence_info"]["master_order"]["order_values"]["entered_inputs"][field.name]){
        
        attribute_val=this.orderDetails["recurrence_info"]["master_order"]["order_values"]["entered_inputs"][field.name]   
    }else{
      attribute_val = field.currentValue
    }
  
    if(attribute_val=='' && this.isAutoSuggestion){
      attribute_val = field.placeholder
    }

    const attributeInfo = {
      id: this.id,
      name: constants.CURRENT_VALUE,
      value: attribute_val
    };
    let input = {}; let selectedIteminfo={};
    input[field.name] = attribute_val;
    if( !constants.SINGLE_VALUE_FIELDS.includes(field.fieldType)){
      let selected_val =''
      if (this.orderDetails && this.orderDetails["recurrence_info"] && this.orderDetails["recurrence_info"]["master_order"] && 
        this.orderDetails["recurrence_info"]["master_order"]["order_values"] && 
        this.orderDetails["recurrence_info"]["master_order"]["order_values"]["selected_options"] &&  
        this.orderDetails["recurrence_info"]["master_order"]["order_values"]["selected_options"][field.name]){
          selected_val=this.orderDetails["recurrence_info"]["master_order"]["order_values"]["selected_options"][field.name]
        }else{
          selected_val = field.currentValue
        }

      selectedIteminfo = {
        id: this.id,
        name: constants.CURRENT_VALUE,
        value: selected_val
      };
      input[field.name]= selected_val
     
    }
 
    constants.SINGLE_VALUE_FIELDS.includes(field.fieldType) ?
          this.$store.dispatch('orders/updateEnteredInputs', input): this.$store.dispatch('orders/updateSelectedOptions', input);
    constants.SINGLE_VALUE_FIELDS.includes(field.fieldType) ? this.$store.dispatch('dynamic_form/updateFieldAttribute', attributeInfo):  
          this.$store.dispatch('dynamic_form/updateFieldAttribute', selectedIteminfo);

    
  },
  computed: {
    ...mapGetters({
      'suggestions': 'dynamic_form/suggestions',
      'getFieldById': 'dynamic_form/getFieldById',
      'enable_group_order': 'dynamic_form/enable_group_order',
      'group_order_field_id': 'dynamic_form/group_order_field_id',
      'groups' : 'machine-groups/groups',

    }),
    currentField() { 
        return this.getFieldById(this.id);
     
    },
    errors() {
      return this.currentField.errors ? this.currentField.errors.join(", ") : "";
    },
    isAutoSuggestion() {
      return result(this.currentField, 'validations[0].type') === "AUTO_SUGGESTION"
    },
    isAutoSuggestionMulti() {
      return result(this.currentField, 'validations[0].type') === "AUTO_SUGGESTION" && result(this.currentField, 'validations[0].autoSuggestionType') === "multiple"
    },
    isOfflineValidation() {
      return result(this.currentField, 'validations[0].type') === "OFFLINE_VALIDATION"
    }
  },
  methods: {
    async validate() {
      const validationEngine = new ValidationEngine();
      this.errorMessages = ["Loading..."];
      this.errorMessages = await validationEngine.run(this.currentField.validations, this.currentField.currentValue, this.auto_suggest_display);
      const attributeInfo = {
        id: this.id,
        name: constants.ERRORS,
        value: Object.assign([], this.errorMessages)
      };
      this.$store.dispatch('dynamic_form/updateFieldAttribute', attributeInfo);
    },

    async getAutoSuggestions(input) {
      let autoSuggestion = this.currentField.validations[0]
      if(input.length < 2) {
        this.$store.dispatch('dynamic_form/setSuggestions', [])
        if(autoSuggestion.autoSuggestionType!='multiple'){
          return new Promise(() => {
            throw new Error('Enter at least 2 characters')
          });
        }
      }
      const validationEngine = new ValidationEngine()
      // let autoSuggestion = this.currentField.validations[0]
      if(autoSuggestion.autoSuggestionType!='multiple'){
        return validationEngine.suggest(autoSuggestion, input)
      }else{
       validationEngine.suggest(autoSuggestion, input).then((res)=>{
         console.log(res);
         this.$store.dispatch('dynamic_form/setSuggestions', res.data)
         return;
       }).catch((err)=>{
         console.log(err);
       })
      }
      
    },

   

   

    update(event) {
      const attributeInfo = {
        id: this.id,
        name: constants.CURRENT_VALUE,
        value: event.target.value
      };
      let el = event.target;
      let input = {};
      input[el.name] = el.value;
      constants.SINGLE_VALUE_FIELDS.includes(this.currentField.fieldType) ?
        this.$store.dispatch('orders/updateEnteredInputs', input) : this.$store.dispatch('orders/updateSelectedOptions', input);
      this.$store.dispatch('dynamic_form/updateFieldAttribute', attributeInfo);
    },

    clearSelection() {
      this.updateSelectedSuggestion({})
    },

   

    updateSelectedSuggestion(selectedSuggestion) {
      //selectedSuggestion.hasOwnProperty("display")
      
      if (Object.prototype.isPrototypeOf.call("display", selectedSuggestion)){
        this.auto_suggest_display = selectedSuggestion.display
      }
      const attributeInfo = {
        id: this.id,
        name: constants.CURRENT_VALUE,
        value: selectedSuggestion.value
      };
      let input = {};
      input[this.currentField.name] = selectedSuggestion.value;
      constants.SINGLE_VALUE_FIELDS.includes(this.currentField.fieldType) ?
      this.$store.dispatch('orders/updateEnteredInputs', input) :
      this.$store.dispatch('orders/updateSelectedOptions', input);
      this.$store.dispatch('dynamic_form/updateFieldAttribute', attributeInfo);
    }
  }
};

export default dynamicFormMixin;
