<template>
  <div class="padding">
    <p>{{currentField.displayName}}
    <dxcTooltip :tooltiptext="currentField.description"></dxcTooltip>
    </p>
    <div  v-for="option of currentField.options" :key="option.id">
      <input type="radio"
             class="radio-button"
             :id="option.id"
             :value="option.value"
             v-model="selectedValue"
             @blur="validate">
      <label class="radio-button-label">{{ option.displayValue }}</label>
    </div>
    <p class="error-text"> {{ errors }}</p>
  </div>
</template>

<script>
  import dynamicFormMixin from "../../dynamic-form-mixin";
  import DxcTooltip from '../dxc-tooltip/index'
  export default {
    components:{
        dxcTooltip: DxcTooltip

      },
    mixins: [dynamicFormMixin],
    data() {
      return {
        selectedValue: '',
      }
    },
    watch: {
      selectedValue: function () {
        this.update({target: {name: this.currentField.name, value: this.selectedValue}});
      }
    },
    mounted() {
      this.selectedValue = this.currentField.currentValue;
    },
  }
</script>

<style lang="sass" scoped>
  @import '../../../assets/styles/includes'
  .padding
    padding: 0 1.5rem 1.5rem

  .field-width
    width: 25rem

  .radio-button
    width: 1.25rem
    height: 1.25rem
    margin-right: 1rem

  .radio-button-label
    font-size: 1rem

  .error-text
    font-size: 0.7rem
    color: red
</style>
