import Validation from "./validation";
import {$} from "../../plugins/axios";

const validationNamesMapping = {
  'user': 'adUserValidation',
  'group': 'adGroupValidation'
};

export class ActiveDirectoryValidation extends Validation {
  async validate(leftOperand, rightOperand) {
    const payload = {
      attribute_name: this.config.attributeName,
      validation_name: validationNamesMapping[this.config.entityType],
      value: leftOperand
    };
    return await $()({ method: 'post', url: `validate`, data: payload }).then(() => {
      return this.yield(true);
    }).catch(() => {
      return this.yield(false);
    });
  }
}
