import { $ } from '../plugins/axios'
import Vue from 'vue'
import newBlueprint from '../static/seed/factory/blueprint'

const state = () => ({
  blueprints: null,
  blueprint: null,
  message: null,
  success: false,
  configValidationResult: null
})

const getters = {
  blueprints: state => {
    return state.blueprints
  },
  blueprint: state => {
    return state.blueprint
  },
  success: state => {
    return state.success
  },
  message: state => {
    return state.message
  },
  configValidationResult: state => {
    return state.configValidationResult
  }
}

const mutations = {
  getBlueprints(state, blueprints) {
    state.blueprints = blueprints
  },
  appendBlueprints(state, blueprint) {
    Vue.set(state.blueprints, state.blueprints.length, blueprint)
  },
  getBlueprint(state, blueprint) {
    state.blueprint = blueprint
  },
  updateBlueprint: (state, blueprint) => {
    state.blueprint[blueprint.key] = blueprint.value
  },
  flashMessage: (state, flash) => {
    state.message = flash.message
    state.success = flash.success
  },
  clearMessage: (state) => {
    state.message = ''
    state.success = false
  },
  setConfigValidationResultForBlueprint: (state, configValidationResult) => {
    state.configValidationResult = configValidationResult
  }
}

const actions = {
  getNewBlueprint(context) {
    context.commit('getBlueprint', newBlueprint)
  },
  updateBlueprint(context, blueprint) {
    context.commit('updateBlueprint', blueprint)
  },
  resetBlueprint (context) {
    context.commit('getBlueprint', null)
  },
  async getBlueprints(context) {
    return $()({method: 'get', url: 'blueprints'})
      .then((res) => {
        context.commit('getBlueprints', res.data)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  async getBlueprint(context, id) {
    return $().get(`blueprints/${id}`)
      .then((res) => {
        context.commit('getBlueprint', res.data)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  async validateUserOrgCredentialsForBlueprint(context, id) {
    return $().get(`blueprints/${id}/configs/validate`)
      .then((res) => {
        context.commit('setConfigValidationResultForBlueprint', res.data)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  resetConfigValidationResultForBlueprint(context) {
    context.commit('setConfigValidationResultForBlueprint', null)
  },
  async postBlueprint(context, blueprint) {
    context.commit('clearMessage')
    $()({ method: 'post', url: `blueprints`, data: blueprint })
      .then((res) => {
        const flash = { success: true, message: 'Blueprint added!' }
        context.commit('appendBlueprint', res.data)
        context.commit('getBlueprint', null)
        context.commit('flashMessage', flash)
        setTimeout(function () {
          context.commit('clearMessage')
        }, 2000)
      })
      .catch(function (error) {
        const flash = { success: false, message: error.response.data.messages.join(', ') }
        context.commit('flashMessage', flash)
        setTimeout(function () {
          context.commit('clearMessage')
        }, 2000)
      })
  },
}

export default { namespaced:true,state, getters, mutations, actions }
