const getBearerToken = () => {
  let bearerToken = null;
  if (!process.server) {
    if (localStorage) {
      bearerToken = localStorage.getItem('auth_token') || localStorage.getItem('token');
    }
  }
  return 'Bearer eyJhbGciOiJIUzI1NiJ9..QkVZ9iBRwvzE5Dv46odbR8fdhx7oyyBB3OIw0UZcDhM'
  //return bearerToken;
};

const getAuthToken = () => {
  let bearerToken = getBearerToken();
  let authToken = null;
  if(bearerToken != null){
    authToken = bearerToken.replace("Bearer ", "")
  }
  return authToken;
};

function redirectToLoginPage() {
  window.location.href = '/'
}

function resetLocalStorage() {
  localStorage.removeItem('auth_token')
  localStorage.removeItem('whoami')
}

const $ = () => {
  let client = axios.create({
    //baseURL: process.env.PORTAL_BACKEND + '/dxc/api/',

    baseURL: 'https://apidev.digitalxc.com/pb-dev'+ '/dxc/api/',
    headers: {
      'Content-Type': 'application/json',
      'Authorization': getBearerToken(),
      'Accept': 'application/json'
    }
  });

  client.interceptors.request.use(config => {
    config.metadata = { startTime: Date.now()}
    return config;
  }, error => Promise.reject(error));

  client.interceptors.response.use(
    function (response) {
      let auth_token = response.headers.authorization;
      if(!isEmpty(auth_token)){
        localStorage.setItem('auth_token', auth_token);
      }
      return response;
    },
    function (error) {
      let isUnAuthorized = result(error, 'response.status') === 401
      if (isUnAuthorized) {
        resetLocalStorage()
        redirectToLoginPage()
        throw error;
      }
      else {
        throw error;
      }
    }
  );
  return client;
};


const $jobEngine = () => axios.create({
  baseURL: process.env.JOB_ENGINE + '/',
  headers: {
    'Authorization': `${getAuthToken()}`,
    'Accept': 'application/json'
  }
});

const $unauthorized = axios.create({
  baseURL: process.env.PORTAL_BACKEND,
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json'
  }
});

const currentUser = async () => await jwt(getAuthToken())

const $s3 = new axios.create();
delete $s3.defaults.headers['authorization'];

export { $, $unauthorized, $jobEngine, $s3, auth_token, getAuthToken,  currentUser}
