<template lang="pug">
div 
  .page.home(v-if="tenant.image_urls_qualified")
    .mask(:style="{'background-image': 'url(' + getHomePageImageUrl() + ')' }")
    main
      br/
      .container
        h3.section-title Browse Products
      .container
        product-grid(:products="products", v-if="products.length>0")
  div(id="productScrollSensor")
</template>

<script>
import { mapGetters } from 'vuex'
import {isUndefined} from 'lodash-es'




//import AppHeader from '@/components/common/AppHeader'
//import Search from '@/components/product/Search'
import ProductGrid from '@/components/product/ProductGrid'
//import Featured from '@/components/product/FeaturedProducts'
import scrollMonitor from 'scrollmonitor';

const ALLOWED_PATHS_FOR_RELOAD = ['/home'];
export default {
  scrollToTop: true,
  async mounted() {
    await this.$store.dispatch('published_products/getProductsByPage');
    
  },
  async beforeMount() {
    await this.getCurrentTenant();
  },
  created () {
    // this.$store.dispatch('published_products/resetSearch');
    this.$store.dispatch('published_products/getProductsByPage');
    // this.$store.dispatch('categories/getAllCategories');
  },
  computed: {
    ...mapGetters({
      products: 'published_products/products',
      //featured: 'published-products/featured',
      //categories: 'categories/categories',
      //all_categories: 'categories/allCategories',
      tenant: 'tenants/tenant'
    }),
  },
  methods: {
    getHomePageImageUrl() {
      console.log("hwreeee" + this.tenant);
      if(!isUndefined(this.tenant.image_urls_qualified)) {
        return this.tenant.image_urls_qualified[2]!="NA"
          ? this.tenant.image_urls_qualified[2]
          : "/images/tenant-placeholder.jpg"
      } else {
        return "/images/tenant-placeholder.jpg"
      }
    },
    getCurrentTenant: async function() {
      await this.$store.dispatch('tenants/getCurrentTenant')
    },
   scroll(publishedProductsFetchDebounce) {
     window.onscroll = () => {
       let bottomOfWindow = document.documentElement.scrollTop + window.innerHeight === document.documentElement.offsetHeight;
       if (bottomOfWindow && ALLOWED_PATHS_FOR_RELOAD.includes(window.location.pathname)) {
         publishedProductsFetchDebounce();
       }
     }
   }
  },
  beforeDestroy() {
    this.$store.dispatch('published_products/resetPublishedProducts');
  },
  components: {
    //AppHeader,
    ProductGrid,
    //Search,
    //Featured
  },
}
</script>

