import { $ } from '../plugins/axios'
import { sortByProperty } from '../middleware/utils'
import Vue from 'vue'
const state = () => ({
  schema: [],
  suggestions: [],
  recurrence_type: [],
  enable_group_order: false,
  group_order_field_id: ''
});

const getters  = {
  schema: (state) => {
    return state.schema
  },
  suggestions: (state) => {
    return state.suggestions
  },
  getFieldById: (state) => (id) => {
    return state.schema.find( f => f.id === id);
  },
  getFieldByName: (state) => (name) => {
    return state.schema.find( f => f.name === name);
  },
  hasField: (state) => (name) => {
    return !!state.schema.find(f => f.name === name);
  },
  recurrence_type : state => state.recurrence_type,
  enable_group_order: state => state.enable_group_order,
  group_order_field_id: state => state.group_order_field_id

};

const mutations = {
  addField: (state, field) => {
    state.schema.push(field);
  },
  updateFieldAttribute: (state, attributeInfo) => {
    const index = state.schema.findIndex(field => field.id === attributeInfo.id);
    if (index > -1) {
      const field = {...state.schema[index]};
      field[attributeInfo.name] = attributeInfo.value;
      Vue.set(state.schema, index, field);
    }
  },
  updateFieldAttributeByName: (state, attributeInfo) => {
    const index = state.schema.findIndex(field => field.name === attributeInfo.name);
    if (index > -1) {
      const field = {...state.schema[index]};
      field[attributeInfo.attributeName] = attributeInfo.value;
      Vue.set(state.schema, index, field);
    }
  },
  setSchema: (state, schema) => {
    state.schema = schema;
  },
  setSuggestions: (state, suggestions) => {
    state.suggestions = suggestions;
  },
  setReccurence_type: (state, recurrence_type) => {
    state.recurrence_type = recurrence_type
  },
  removeGroupSuggestion: (state) => {
    state.schema.forEach((sc)=>{
      if(Object.prototype.hasOwnProperty.call('validations',sc)){
        var i = sc.validations.length;
        while (i--){
          //sc.validations[i].hasOwnProperty('suggestion_type')
          if (Object.prototype.hasOwnProperty.call('suggestion_type', sc.validations[i])  && sc.validations[i].suggestion_type === "groupsSuggestion"){
            state.enable_group_order = true;
            state.group_order_field_id = sc.id
            sc.validations.splice(i,1);

          }
        }
      }
    })
  }
};

const actions = {
  removeGroupSuggestion: async (context) =>{
    await context.commit('removeGroupSuggestion')
  },
  setSchema: (context, schema) => {
    if(Object.keys(schema).length !== 0) {
      schema.sort(sortByProperty("field_position"))
    }
    context.commit('setSchema', schema);
  },
  setSuggestions: (context, suggestions) => {
    context.commit('setSuggestions', suggestions);
  },
  addField: (context, field) => {
    const existingField = context.getters.getFieldByName(field.name);
    if (!existingField) {
      context.commit('addField', field);
    }
  },
  updateFieldAttribute: (context, attributeInfo) => {
    context.commit('updateFieldAttribute', attributeInfo);
  },
  updateFieldAttributeByName: (context, attributeInfo) => {
    context.commit('updateFieldAttributeByName', attributeInfo);
  },

  async fetchReccurence_type(context){
    return $().get('/recurrence_types')
      .then((res)=>{
        context.commit('setReccurence_type',res.data)
      })
      .catch((e)=>{
        console.log(e);
      })
  }
};

export default { namespaced: true, state, getters, mutations, actions }
