import { ActiveDirectoryAutoSuggestion } from './auto-suggestions/active-directory-validation'
import OfflineValidationFactory from './offline-validations/offline-validation-factory'

export default class ValidationFactory {
  getValidation(validation) {
    const { type } = validation;
    const validations = {
      'OFFLINE_VALIDATION': OfflineValidationFactory.getOfflineValidation(validation.validationType),
      'AUTO_SUGGESTION': new ActiveDirectoryAutoSuggestion()
    };
    return validations[type];
  }
}
