export default class Validation {
  validate(leftOperand, rightOperand){}

  constructor() {
    this.config = {};
  }

  setConfig(config) {
    this.config = config;
    return this;
  }

  canAllowOrder() { return this.config.allowOrder === 'allow'; }

  yield(result) {
    return (result && this.canAllowOrder()) || (!result && !this.canAllowOrder())
  }
}
