import Validation from "./validation";
import {$} from "../../plugins/axios";

const validationNamesMapping = {
  'user': 'adUserAutoSuggestion',
  'group': 'adGroupAutoSuggestion',
  'teams': 'teams'
};

export class ActiveDirectoryAutoSuggestion extends Validation {
  async validate(leftOperand, rightOperand) {
    var payload;
    if (this.config.suggestion_type != "teamsSuggestion"){
       payload = {
        attribute_name: this.config.attributeName,
        suggestion_name: validationNamesMapping[this.config.entityType],
        value: leftOperand
      };
    }else if (this.config.suggestion_type === "teamsSuggestion"){
      payload = {
        suggestion_type: this.config.suggestion_type,
        suggestion_name: validationNamesMapping[this.config.entityType],
        value: rightOperand
      };
    }
    return await $()({ method: 'post', url: `auto_suggestions/1/match`, data: payload }).then(() => {
      return this.yield(true);
    }).catch(() => {
      return this.yield(false);
    });
  }
  async suggest(operand) {
    var payload;
    if (this.config.suggestion_type === "dynamicSuggestion"){
      payload = {
        suggestion_type: this.config.suggestion_type,
        suggestion_id: this.config.validationID,
        value: operand
      };
    }
    else if (this.config.suggestion_type === "teamsSuggestion"){
      payload = {
        suggestion_type: this.config.suggestion_type,
        suggestion_name: validationNamesMapping[this.config.entityType],
        value: operand
      };
    }else{
      payload = {
       attribute_name: this.config.attributeName,
       suggestion_name: validationNamesMapping[this.config.entityType],
       value: operand
     };
   }
  //  return $()({ method: 'post', url: `auto_suggestions/1/match`, data: payload })
   if(this.config.suggestion_type === "dynamicSuggestion"){
    return $()({ method: 'post', url: `auto_suggestions/1/match`, data: payload })
          .then((res)=>{
              function add_id_name(o){
                var tf = res.data.transform_function
                var id_value = tf["id"]
                var name_value = tf["name"]
                if (Object.prototype.hasOwnProperty.call(o, id_value) && Object.prototype.hasOwnProperty.call(o, name_value)){
                  return {...o, id:o[id_value],name:o[name_value]}
                }else if(Object.prototype.hasOwnProperty.call(o, id_value)){
                  return {...o, id:o[id_value]}
                }else if(Object.prototype.hasOwnProperty.call(o, name_value)){
                  return {...o, name:o[name_value]}
                }
                else{
                  return {...o, id:null}
                }
                
              }

              var tf = res.data.transform_function
              var loop_value = tf["array_name"]
              var data_mod;
              if(Object.prototype.hasOwnProperty.call(res.data.result, loop_value)){
                 data_mod = res.data.result[loop_value].map(add_id_name)
              }else{
                 data_mod = res.data.result.map(add_id_name)
              }
              // const data_mod = res.data.result.map(add_id_name) 
               const myPromise = new Promise((resolve,reject) => {
                 var final_res = {
                   data: data_mod,
                   status: 200,
                   request:{status:200}
                 }
                  resolve(final_res)
              });
              return myPromise
          })
          .catch((e) => {
            console.log(e)
          })
  }
  else{
    return $()({ method: 'post', url: `auto_suggestions/1/match`, data: payload })
  }
    // return $()({ method: 'post', url: `auto_suggestions/1/match`, data: payload })
  }

  async suggestUser(operand) {
    const payload = {
      attribute_name: 'name',
      suggestion_name: validationNamesMapping.user,
      value: operand
    };
    return $()({ method: 'post', url: `auto_suggestions/1/match`, data: payload })

  }

  async suggestGroup(operand) {
    const payload = {
      attribute_name: 'name',
      suggestion_name: validationNamesMapping.group,
      value: operand
    };
    return $()({ method: 'post', url: `auto_suggestions/1/match`, data: payload })

  }
}
