import Vue from 'vue'
import {filter,isEmpty,find} from 'lodash-es'
import { $ } from '../plugins/axios'
import { newProduct } from '../static/seed/factory/product'
import { newMasterVariant } from '../static/seed/factory/product'
import { fireIfLatest } from '../middleware/utils'
const state = () => ({
    currentPage: 1,
    products: [],
    product: null,
    search: null,
    searching: false,
    lastSearchedTime: Date.now(),
    productsByCategory: [],
    productsByCategoryPage: 1
})

const getters = {
    currentPage: state => state.currentPage,
    products: state => {
        return state.products
    },
    lastSearchedTime: state => state.lastSearchedTime,
    featured: state => {
        return filter(state.products, p => {
            if (!isEmpty(p))
                return find(p.tags, t => t.name === '_featured')
        })
    },
    search: state => {
        return state.search
    },
    searching: state => {
        return state.searching
    },
    product: state => {
        return state.product
    },
    productDescription: state => {
        return state.product ? state.product.description : null
    },
    findProductsByCategory: state => category_id => {
        const result = filter(state.products, p => {
            return find(p.categories, c => (c.id == category_id))
        })
        return result
    },
    findProductsByTag: state => tag_id => {
        return filter(state.products, p => {
            return find(p.tags, t => (t.id === tag_id))
        })
    },
    variantId: state => state.product && state.product.master.id,
    productsByCategory: state => state.productsByCategory,
    productsByCategoryPage: state => state.productsByCategoryPage
}

const mutations = {
    incrementCurrentPage(state) {
        state.currentPage = state.currentPage + 1;
    },
    resetCurrentPage(state) {
        state.currentPage = 1;
    },
    getProduct: (state, product) => {
        state.product = product
        if (!product.master)
            Vue.set(product, 'master', newMasterVariant)
    },
    getProductDescription: (state, description) => {
        state.product.description = description
    },
    getProducts: (state, products) => {
        state.products = products;
    },
    linkJob(state, job) {
        Vue.set(state.product, 'job_id', job)
    },
    resetSearch(state) {
        state.search = []
    },
    searching(state, flag) {
        state.searching = flag
    },
    setLastSearchedTime: (state) => {
        state.lastSearchedTime = Date.now()
    },
    populateSearch(state, results) {
        Vue.set(state, 'search', results)
    },
    setProductsByCategory(state, productsByCategory) {
        state.productsByCategory = productsByCategory
    },
    incrementCurrentProductsByCategoryPage(state) {
        state.productsByCategoryPage = state.productsByCategoryPage + 1;
    },
    resetProductsByCategory(state) {
        state.productsByCategory = []
    },
    resetCurrentProductsByCategoryPage(state) {
        state.productsByCategoryPage = 1
    }
}

const actions = {
    resetPublishedProducts(context) {
        context.commit('getProducts', []);
        context.commit('resetCurrentPage');
    },
    getNewProduct(context) {
        context.commit('getProduct', newProduct)
    },
    getProduct(context, product) {
        context.commit('getProduct', product)
    },
    getProductDescription(context, description) {
        context.commit('getProductDescription', description)
    },
    updateProduct(context, product) {
        context.commit('updateProduct', product)
    },
    clearMessage(context) {
        context.commit('clearMessage')
    },
    async getProductById(context, product_id) {
        return $().get(`published_products/${product_id}`)
            .then((res) => {
                context.commit('getProduct', res.data)
            })
            .catch((e) => {
                console.log(e)
            })
    },
    async getProducts(context) {
        return $().get('published_products')
            .then((res) => {
                context.commit('getProducts', res.data)
            })
            .catch((e) => {
                console.log(e)
            })
    },
    async getProductsByPage(context) {
        const page = context.getters.currentPage;
        context.commit('incrementCurrentPage');
        return $().get(`published_products?page=${page}`)
            .then((res) => {
                const existingProducts = context.getters.products;
                const currentProducts = [...existingProducts, ...res.data];
                context.commit('getProducts', currentProducts);
            })
            .catch((e) => {
                console.log(e)
            })
    },
    resetSearch(context) {
        context.commit('resetSearch')
    },
    search(context, q) {
        context.commit('resetSearch')
        q = q.replace(/ /g, '+')

        context.commit('searching', true)
        return $().get('published_products/search?name=' + q)
            .then((res) => {
                fireIfLatest.call(context, () => {
                    context.commit('searching', false)
                    context.commit('populateSearch', res.data)
                }, res)
            })
            .catch((e) => {
                context.commit('searching', false)
                console.log(e)
            })
    },
    findProductsByTag(context, q) {
        q = q.replace(/ /g, '+')
        return $().get('published_products/search?tag=' + q)
            .then((res) => {
                fireIfLatest.call(context, () => {
                    context.commit('getProducts', res.data)
                }, res)
            })
            .catch((e) => {
                context.commit('searching', false)
                console.log(e)
            })
    },
    getProductsByCategory(context, categoryId) {
        const page = context.getters.productsByCategoryPage;
        context.commit('incrementCurrentProductsByCategoryPage');
          return $().get(`categories/${categoryId}/published_products?page=${page}`)
            .then((res) => {
              const existingProducts = context.getters.productsByCategory;
              const currentProducts = [...existingProducts, ...res.data];
              context.commit('setProductsByCategory', currentProducts);
            })
            .catch((e) => {
              console.log(e)
            })
    },
    resetProductsByCategory(context) {
        context.commit('resetProductsByCategory');
        context.commit('resetCurrentProductsByCategoryPage')
    }
}

export default { namespaced:true, state, getters, mutations, actions }
