<template lang="pug">
span
  span(v-if="shouldExpand") {{ trimmed }}
  span(v-else) {{ text }}
  span(v-if="showExpander")
    span(v-if="shouldExpand") ...
    | &nbsp;
    a(@click="toggle") {{ label }}
</template>

<script>
export default {
  props: {
    text: String,
    min: { type: Number, default: 200 }
  },
  data () {
    return {
      shouldExpand: false
    }
  },
  mounted () {
    this.shouldExpand = (this.text.length > this.min)
  },
  computed: {
    trimmed () {
      return this.text.substr(0, this.min)
    },
    label () {
      return this.shouldExpand ? "show more" : "show less"
    },
    showExpander () {
        return this.text.length > this.min
    }
  },
  methods: {
    toggle () {
      this.shouldExpand = !this.shouldExpand
    }
  }
}
</script>

<style lang="sass" scoped>
a
  cursor: pointer
  font-style: normal
  font-weight: lighter
</style>


