import { $ } from '../plugins/axios'
import {findIndex,merge,filter, isEmpty} from 'lodash-es'
import Vue from 'vue'

const state = () => ({
  userConfigs: [],
  userConfigValues: [],
  message: null,
  success: null
})

const getters = {
  userConfigs: state => {
    return state.userConfigs
  },
  userConfigValues: state => {
    return state.userConfigValues
  },
  message: state => {
    return state.message
  },
  success: state => {
    return state.success
  }
}

const mutations = {
  getUserConfigs(state, userConfigs) {
    state.userConfigs = userConfigs
  },
  getUserConfigValues(state, userConfigValues) {
    state.userConfigValues = userConfigValues
  },
  flashMessage: (state, flash) => {
    state.message = flash.message
    state.success = flash.success
  },
  clearMessage: (state) => {
    state.message = ''
    state.success = false
  },
  updateConfigValue: (state, configValue) => {
    let index = findIndex(state.userConfigValues, {user_config_id: configValue.user_config_id});
    let configIndex = index === -1 ? state.userConfigValues.length : index;
    let value = state.userConfigValues[index] || {};

    Vue.set(state.userConfigValues, configIndex, merge(value, configValue))
  },
  saveConfigValues: (state, configValues) => {
    let configIds = configValues.map(v => v.id);
    state.userConfigValues = state.userConfigValues.filter(v => !configIds.includes(v.id)).concat(configValues)
  }
}

const actions = {
  async getUserConfigs (context) {
    return $().get(`user_configs`)
      .then((res) => {
        context.commit('getUserConfigs', res.data)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  async getUserConfigValues (context) {
    return $().get(`user_config_values`)
      .then((res) => {
        context.commit('getUserConfigValues', res.data)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  updateUserConfigValue(context, data) {
    context.commit('updateConfigValue', data)
  },
  saveUserConfigValues(context, vendor) {

    let userConfigIds = context.state.userConfigs
                                    .filter(v => v.vendor === vendor)
                                    .map( v => v.id);

    return $()({ method: 'post',
        url: `user_config_values/bulk`,
        data: {user_config_values: context.state
                                          .userConfigValues
                                          .filter(v => userConfigIds.includes(v.user_config_id))} })
      .then((res) => {

        let errors = res.data.map(config => config.errors)
                          .filter(error => !isEmpty(error))

        let message = isEmpty(errors)? "configuration saved!" :
                                        errors.map( error => Object.keys(error)
                                                                    .map(k => `${k} ${error[k].join(" & ")}`) )
                                              .join(", ")

        const flash = { success: isEmpty(errors), message: message };
        context.commit('saveConfigValues', res.data);
        context.commit('flashMessage', flash);

        if(isEmpty(errors)){
          setTimeout(function () {
            context.commit('clearMessage')
          }, 2000)
        }
      })
      .catch(function (error) {
        const flash = { success: false, message: 'Error while saving configuration!' }
        context.commit('flashMessage', flash)
        setTimeout(function () {
          context.commit('clearMessage')
        }, 2000)
      })
  }
}

export default {namespaced:true,state, getters, mutations, actions }
