import Vue from 'vue'
import {findIndex} from 'lodash-es'
import { $, $s3, auth_token, currentUser } from '../plugins/axios'
import newTenant from '../static/seed/factory/tenants'

const state = () => ({
  tenants: [],
  tenant: {
    subscription: []
  },
  message: null,
  success: false,
  imageMessage: null,
  imageSuccess: false,
  imageLoading: false
})

const getters = {
  tenants: state => {
    return state.tenants
  },
  tenant: state => {
    return state.tenant
  },
  success: state => {
    return state.success
  },
  message: state => {
    return state.message
  },
  imageSuccess: state => {
    return state.imageSuccess
  },
  imageMessage: state => {
    return state.imageMessage
  },
  imageLoading: state => {
    return state.imageLoading
  },
}
const mutations = {
  getTenants(state, tenants) {
    state.tenants = tenants
  },
  getTenant(state, tenant) {
    state.tenant = tenant
    let i = findIndex(state.tenants, { id: tenant.id })
    if (i > -1) {
      Vue.set(state.tenants, i, tenant)
    } else if (tenant.id) {
      Vue.set(state.tenants, state.tenants.length, tenant)
    }
  },
  updateTenant: (state, tenant) => {
    state.tenant[tenant.key] = tenant.value
  },
  flashMessage: (state, flash) => {
    state.message = flash.message
    state.success = flash.success
  },
  clearMessage: (state) => {
    state.message = ''
    state.success = false
  },
  flashImageMessage: (state, flash) => {
    state.imageMessage = flash.message
    state.imageSuccess = flash.success
  },
  clearImageMessage: (state) => {
    state.imageMessage = ''
    state.imageSuccess = false
  },
  imageLoading: (state, flag) => {
    state.imageLoading = flag
  },
}

const actions = {
  getNewTenant(context) {
    context.commit('getTenant', newTenant)
  },
  updateTenant(context, tenant) {
    context.commit('updateTenant', tenant)
  },
  async getTenants(context) {
    return $().get(`tenants`)
      .then((res) => {
        context.commit('getTenants', res.data)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  async getTenant(context, tenant) {
    return $().get(`tenants/${tenant.id}`)
      .then((res) => {
        context.commit('getTenant', res.data)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  async postTenant(context, tenant) {
    context.commit('clearMessage')
    $()({ method: 'post', url: `tenants`, data: tenant })
      .then((res) => {
        const flash = { success: true, message: 'tenant added!' }
        context.commit('getTenant', res.data)
        context.commit('flashMessage', flash)
        setTimeout(function () {
          context.commit('clearMessage')
        }, 2000)
      })
      .catch(function (error) {
        const flash = { success: false, message: error.response.data.messages.join(', ') }
        context.commit('flashMessage', flash)
        setTimeout(function () {
          context.commit('clearMessage')
        }, 2000)
      })
  },
  async putTenant(context, tenant) {
    if (tenant.id) {
      context.commit('clearMessage')
      $()({ method: 'put', url: `tenants/${tenant.id}`, data: tenant })
        .then((res) => {
          const flash = { success: true, message: 'tenant updated!' }
          context.commit('getTenant', res.data)
          context.commit('flashMessage', flash)
          setTimeout(function () {
            context.commit('clearMessage')
          }, 2000)
        })
        .catch(function (error) {
          const flash = { success: false, message: error.response.data.messages.join(', ') }
          context.commit('flashMessage', flash)
          setTimeout(function () {
            context.commit('clearMessage')
          }, 2000)
        })
    } else {
      context.dispatch('postTenant', tenant)
    }
  },
  async subscribe(context, pl) {
    $()({ method: 'put', url: `tenants/${pl.tenant.id}`, data: pl.subscription_ids })
      .then((res) => {
        context.dispatch('getTenant', res.data)
      })
  },
  uploadLogo(context, image) {
    if (image.tenant && image.blob) {
      context.dispatch('postImage', image)
    }
  },
  setFlashImageMessage(context, flash){
    context.commit('flashImageMessage', { message: flash.message, success: flash.success })
    setTimeout(function () {
      context.commit('clearImageMessage')
    }, 2000)
  },
  async postImage(context, image) {
    context.commit('imageLoading', true)
    $()({ method: 'post', url: `tenants/${image.tenant.id}/images`, data: { alt_text: image.alt } })
      .then(async (res) => {
        const id = res.data.id
        const s3 = res.data.url
        await $s3({ method: 'put', url: s3, data: image.blob, headers: { "Content-Type": "image/jpeg" } })
        .then(async (res) => {
            $().defaults.headers['Authorization'] = `Bearer ${auth_token}`;
            await $()({ method: 'put', url: `tenants/${image.tenant.id}/images/${id}`, data: { uploaded: true } })
              .then((res) => {
                context.commit('imageLoading', false)
                context.dispatch('tenants/getTenant', image.tenant, { root: true })
                context.commit('flashImageMessage', { message: 'image uploaded!', success: true })
                setTimeout(function () {
                  context.commit('clearImageMessage')
                }, 2000)
              })
          });
      })
      .catch(function (error) {
        console.log(error, '$images')
      })
  },

  async getCurrentTenant(context) {
    const user = await currentUser();
    return $().get(`tenants/${user.user.dxc_tenant_id}`)
            .then((res) => context.commit('getTenant', res.data))
            .catch((e) => console.log(e))
  },

  async getTenantBySearchCriteria(context, searchParams) {
    return $().get(`tenants/readonly_search?domain=${searchParams.name}`)
      .then((res) => {
        return res.data
      })
      .catch((e) => {
        console.log(e)
      })
  }

}
export default { namespaced:true, state, getters, mutations, actions }
