<template lang="pug">
.page.order.create
  //app-header
  main(v-if="this.configValidationResult")
    .message(v-if="message", :class="{ success: success }")
      span(v-html="message")
    .container
      .product-detail-modal
        .modal(v-if="open")
          .mask(@click="hide")
          .dialog(style="align-items: center")
            span(v-html="product.description")
      header
        img(:src="resolveImage()")
        p.bold-text(v-if="product.service_provider") Service Provider:
          span.grey-text() &nbsp;{{ product.service_provider }}
        ul.tags(v-if="product.tags")
          li.tag(v-for="t in product.tags", v-if="isPublicTag(t.name)", :key="genKey([t.permalink, t.id])")
            router-link(:to="resolveTag(t.name)") {{ t.name }}
        a.more-details(@click="toggle", v-if="product.description") Detailed Description
      main.user.inputs.form
        h5.categories(v-if="publishedProduct.categories")
          span(v-for="c in publishedProduct.categories", @click="navigateToCategoryPage(c)") {{ c.name }}
        div.namecontainer
          h1.h2.name {{ product.name + "  |  "}}
            span.grey-text SKU#
              span.bold-text {{" " + product.id}}
        p.desc
          show-more(:text="product.short_description", :min="160")
        p.padded-paragraph(v-if="showUserConfigMissingWarning", style="background-color: beige")
          span The Product requires the user to update
          a(href="/home/profile", style="color:#0000EE") &nbsp; user credentials. &nbsp;
          span Please ensure that the values are updated before proceeding with the order
        p.padded-paragraph(v-if="showTenantConfigMissingWarning", style="background-color: beige")
          span The Product requires Org credentials to be updated. Please contact admin before proceeding with the order
        dynamic-form(:publishedProduct="publishedProduct", :disableOrderButton="disableOrderButton")
</template>

<script>
//import AppHeader from '@/components/common/AppHeader'
import {mapGetters, mapState } from 'vuex'
import {isEmpty, forEach} from 'lodash-es'
import DynamicForm from '../../../dynamic-form/dynamicForm';
import ShowMore from '@/components/common/ShowMore'

export default {
  data() {
    return {
      open: false
    }
  },
  asyncData (context) {
    console.log("the context is=" + context)
    return { id: context.params.id }
  },
  async mounted(){
    this.id= this.$route.params.id
    console.log("in mounted the id is" + this.$route.params.id)
    await this.$store.dispatch('published_products/resetSearch')
    await this.$store.dispatch('products/getProductById', this.id);
    await this.$store.dispatch('products/getPublishedProduct', this.id);
    await this.fetchUserConfig();


  },
  computed: {
    ...mapState({
      variantId: state => state.products.product.master.id,
      publishedProduct: state => state.products.publishedProduct,
      userInputs: function () {
        return isEmpty(this.publishedProduct) ? [] : this.publishedProduct.available_user_inputs
      },
      productOptions: function () {
        return isEmpty(this.publishedProduct) ? [] : this.publishedProduct.allowed_product_options_with_defaults
      },
      success: state => state.orders.success,
      message: state => state.orders.message,
      notLoading: state => !state.orders.loading,
      needsPem: state => state.products.publishedProduct.needs_pem,
      configValidationResult: state => state.blueprints.configValidationResult
    }),
    ...mapGetters({
       product: 'products/product',
       product_success: 'products/success',

      userConfigValues: 'user_configs/userConfigValues'
    }),
    showUserConfigMissingWarning: function() {
      return this.configValidationResult ? !this.configValidationResult.is_user_config_present : false
    },
    showTenantConfigMissingWarning: function() {
      return this.configValidationResult ? !this.configValidationResult.is_tenant_config_present : false
    },
    disableOrderButton: function () {
      return this.showUserConfigMissingWarning || this.showTenantConfigMissingWarning
    }

  },
  components: {
    //AppHeader,
    DynamicForm,
    ShowMore
  },
  methods: {
    isPublicTag (tag) {
      return !(tag.indexOf('_') === 0)
    },
    toggle() {
      this.open = !this.open
    },
    hide() {
      this.open = false
    },
    genKey(keys) {
      let g = ''
      forEach(keys, key => {
        if (key) g += '-' + key
      })
      return g.replace(/\//ig, '-')
    },
    resolveTag(tag) {
      tag = tag ? tag.replace(/ /ig, '+') : tag
      return `/home/tags/${tag.toLowerCase()}`
    },
    resolveImage () {
      const urls = this.product.master.image_urls
      return urls && urls.length
      ? urls[0]
      : '/images/product-placeholder.jpg'
    },
    fetchUserConfig: async function(){
      // await this.$store.dispatch('user-configs/getUserConfigValues');
       await this.$store.dispatch('blueprints/validateUserOrgCredentialsForBlueprint', this.product.dxc_blueprint_id);
       //await that.$store.dispatch('products/getPublishedProducts');
       let input = this.productOptions
            .filter(option => option.product_option.default )
            .map((option) => {
              let v = {};
              v[option.key] = option.product_option.default;
              return v;
            })
            .reduce(function(result, item) {
              var key = Object.keys(item)[0];
              result[key] = item[key];
              return result;
            }, {});
       await this.$store.dispatch('orders/updateSelectedOptions', input)
    },
    navigateToCategoryPage(category) {
      this.$router.push({path : `/categories-home/category/${category.id}`});
    }
  },

  destroyed () {
    this.$store.dispatch('orders/clearMessage')
    this.$store.dispatch('blueprints/resetConfigValidationResultForBlueprint')
  }
}
</script>

