  .container
    background: $white
    padding: $space
    position: relative
    @include flex(flex-start)
    .grey-text
      color: grey
    .bold-text
      font-weight: bold
    header
      width: 20rem
      .more-details
        text-decoration: underline
        cursor: pointer
    .categories
      padding-left: $space
      font-weight: bold
    .tags
      padding-bottom: $space
    .tag > a
      opacity: 0.8
      &:hover
        opacity: 1
    .desc
      padding-left: $space
      font-style: oblique
      color: #696969
      font-weight: 500
    .namecontainer
      padding-left: $space
      display: flex
      align-items: flex-start
    .categories >
      span
        text-decoration: underline
        cursor: pointer
      span:after
        content: ",  "
      span:last-child:after
        content: ""
  .user.inputs
    flex: 1
