import Vue from 'vue'
import {merge} from 'lodash-es'
import { $ } from '../plugins/axios'
import newOrder from '../static/seed/factory/order'

const state = () => ({
    orders: [],
    order: newOrder,
    success: false,
    message: null,
    loading: false,
    currentPage: 1,
    orderDetails: {},
    occurances: [],
    group_order: {}
})

const getters = {
    order: state => state.order,
    orderDetails: state => state.orderDetails,
    userInputs: state => state.order.user_inputs,
    productOptions: state => state.order.product_options,
    success: state => state.success,
    message: state => state.message,
    loading: state => state.loading,
    orders: state => state.orders,
    currentPage: state => state.currentPage,
    occurances: state => state.occurances,
    group_order: state => state.group_order

}

const mutations = {
    updateEnteredInputs: (state, userInput) => {
        merge(state.order.entered_inputs, userInput)
    },
    updateScheduledStartDate: (state, userInput) => {
        state.order.scheduled_start_date_time = userInput
    },
    updateSelectedOptions: (state, userInput) => {
        merge(state.order.selected_options, userInput)
    },
    updateOrder: (state, order) => {
        state.order = order
        state.order.selected_options = {}
        state.order.entered_inputs = {}
        state.order.scheduled_start_date_time = ""
    },
    updateOrders: (state, orders) => {
        state.orders = orders
    },
    loading: (state, loading) => {
        state.loading = loading
    },

    setMessage: (state, { message, isSuccess }) => {
        state.message = message;
        state.success = isSuccess;
    },
    incrementCurrentPage(state) {
        state.currentPage = state.currentPage + 1;
    },
    setCurrentPage(state, number) {
        state.currentPage = number;
    },
    setOrderDetails(state, details) {
        state.orderDetails = details;
    },

    clearMessage: (state) => {
        state.message = '';
        state.success = false
    },

    setOccurances(state, occurances) {
        state.occurances = occurances;
    },

    updateGroupOrder: (state, orders) => {
        state.group_order = orders
    },

}

const actions = {
    async resetOrders(context) {
        await context.commit('updateOrders', []);
        await context.commit('setCurrentPage', 1);
    },

    clearMessage(context) {
        context.commit("clearMessage");
    },
    setGroupOrders(context,data){
        context.commit(`updateGroupOrder`, data)
    },
    updateEnteredInputs(context, data) {
        context.commit('updateEnteredInputs', data)
    },

    updateScheduledStartDate(context, data) {
        context.commit('updateScheduledStartDate', data)
    },
    updateSelectedOptions(context, data) {
        context.commit('updateSelectedOptions', data)
    },
    async createOrder(context, params) {
        let state = context.state
        context.commit('loading', true);
        var data;
        if (Object.keys(state.group_order).length ===2){
            state.order.entered_inputs[state.group_order.name] = "test"
            data = {
                variant_id: params.variantId,
                entered_inputs: state.order.entered_inputs,
                selected_options: state.order.selected_options,
                scheduled_start_date_time: params.scheduled_start_date_time,
                scheduled_end_date_time : params.scheduled_end_date_time,
                recurrence_info : params.recurrence_info_save,
                group_order : { type : "machine", group_id:state.group_order.value.id, name: state.group_order.name}
            }
        }else{
            data = {
                variant_id: params.variantId,
                entered_inputs: state.order.entered_inputs,
                selected_options: state.order.selected_options,
                scheduled_start_date_time: params.scheduled_start_date_time,
                scheduled_end_date_time : params.scheduled_end_date_time,
                recurrence_info : params.recurrence_info_save
            }
        }
        return $()({
                method: 'post',
                url: `orders`,
                data: data
            })
            .then((res) => {
                return context.commit('updateOrder', res.data)
            })
            .catch(function(error) {
                context.commit('loading', false);
                context.commit('setMessage', { message: `Error Saving order: ${error.response.data[0]}. You may link your cloud account <a href="/home/profile" style="color: #0000EE"> here </a>`, isSuccess: false });
                throw error;
            })
    },
    async resetOccurances(context){
        return context.commit('setOccurances',[])
    },
    async getOccurance(context, params){
        var url = `recurrent_order_schedules/next_occurrences?start_date_time=${params.scheduled_start_date_time}&end_date_time=${params.scheduled_end_date_time}&recurrence_type_id=${params.recurrence_info_save.recurrence_type_id}&separation_count=${params.recurrence_info_save.separation_count}`
        if (params.recurrence_info_save.recurrence_type_id===3){
            var week_str=''
            for(let i of params.recurrence_info_save.days_of_week){
                week_str+=i+','
              }
            week_str = week_str.slice(0,-1);
            url = url + `&days_of_week=${week_str}`
        }else if(params.recurrence_info_save.recurrence_type_id===4){
            if(params.recurrence_info_save.day_of_month!=null){
                url += `&day_of_month=${params.recurrence_info_save.day_of_month}`
            }else{
                url += `&days_of_week=${params.recurrence_info_save.days_of_week[0]}&week_of_month=${params.recurrence_info_save.week_of_month}`
            }
        }
        return $()({ method: 'get', url: url })
            .then((res)=>{
                // console.log(res.data['occurrences']);
                return context.commit('setOccurances',res.data['occurrences'])

            })
            .catch((err)=>{
                console.log(err);
            })
    },
    async editOrder(context,  params){
        let state = context.state
        context.commit('loading', true);
        return $()({
            method: 'patch',
            url: `orders/${params.order_id}`,
            data: {
                variant_id: params.variantId,
                entered_inputs: state.order.entered_inputs,
                selected_options: state.order.selected_options,
                scheduled_start_date_time: params.scheduled_start_date_time,
                scheduled_end_date_time : params.scheduled_end_date_time,
                recurrence_info : params.recurrence_info_save,
                days_of_week: params.recurrence_info_save.days_of_week
            }
        })
        .then((res) => {
            return context.commit('updateOrder', res.data)
        })
        .catch((error) => {
            context.commit('loading', false);
            console.log("Error while updating order", error)
            context.commit('setMessage', { message: `Error Saving order: ${error.response.data[0]}. You may link your cloud account <a href="/home/profile" style="color: #0000EE"> here </a>`, isSuccess: false });

        })

    },
    async fetchOrders(context) {
        context.commit('loading', true);
        const page = context.getters.currentPage;
        context.commit('incrementCurrentPage');
        console.log(page);
        return $()({ method: 'get', url: `orders?page=${page}` })
            .then((res) => {
                context.commit('loading', false);
                const existingOrders = context.getters.orders;
                const totalOrders = [...existingOrders, ...res.data];
                return context.commit('updateOrders', totalOrders)
            })
            .catch(function(error) {
                console.log("Error while fetching orders", error)
            })
    },
    async fetchOrder(context, orderId) {
        return $()({ method: 'get', url: `orders/${orderId}` })
            .then((res) => {
                return context.commit('updateOrder', res.data)
            })
            .catch((error) => {
                console.log("Error while fetching order", error)
            })
    },
    async fetchOrderDetails(context, orderId) {
        context.commit('loading', true);
        return $()({ method: 'get', url: `orders/${orderId}/details` })
            .then((res) => {
                context.commit('loading', false);
                return context.commit('setOrderDetails', res.data)
            })
            .catch((error) => {
                console.log("Error while fetching order details", error)
            })
    },
    async downloadUserPem(context, pemId) {
        return $()({ method: 'get', url: `user_pems/${pemId}` })
            .then((res) => {
                return res.data;
            })
            .catch((error) => {
                console.log("Error while downloading pem", error);
            })
    },
    async downloadTenantPem(context, pemId) {
        return $()({ method: 'get', url: `tenant_pems/${pemId}` })
            .then((res) => {
                return res.data;
            })
            .catch((error) => {
                console.log("Error while downloading tenant pem", error);
            })
    },
    async rollback(context, orderId) {
        return $()({ method: 'post', url: `orders/${orderId}/rollback` }).then((res) => {
                return context.commit('updateOrder', res.data)
            })
            .catch((error) => {
                console.log("Error while rolling back", error);
            })
    },
    async fetchOrdersAsAdmin(context) {
        context.commit('loading', true);
        const page = context.getters.currentPage;
        context.commit('incrementCurrentPage');
        return $()({ method: 'get', url: `admin/orders?page=${page}` })
            .then((res) => {
                context.commit('loading', false);
                const existingOrders = context.getters.orders;
                const totalOrders = [...existingOrders, ...res.data];
                return context.commit('updateOrders', totalOrders);
            })
            .catch(function(error) {
                console.log("Error while fetching orders", error)
            })
    },
    async cancelOrder(context, orderId) {
      return $()({ method: 'post', url: `orders/${orderId}/cancel` })
        .catch((error) => {
          console.log("Error while rolling back", error);
        })
    }
}
export default { namespaced: true, state, getters, mutations, actions }
