import ValidationFactory from "./validation-factory";
import {compact} from 'lodash-es'


export default class ValidationEngine {

 async run(validations, leftOperand, rightOperand) {
    const validationFactory = new ValidationFactory();
    let errorMessagesAsPromises = [];
    if (validations) {
      errorMessagesAsPromises = validations.map(async validation => {
        const result = await validationFactory
          .getValidation(validation)
          .setConfig(validation)
          .validate(leftOperand, rightOperand);
        if (!result) {
          return validation.errorMessage || "";
        }
      });
    }
    const errorMessages = await Promise.all(errorMessagesAsPromises);
    return compact(errorMessages);
  }

  async suggest(validation, operand) {
    const validationFactory = new ValidationFactory();
        return validationFactory
          .getValidation(validation)
          .setConfig(validation)
          .suggest(operand);
 }
}
