<template lang="pug">
.div.product.summary(v-if="product")
    .thumb
      div
        img(v-if="product" :src="resolveImage()" v-on:click="resolveProductPage")
      div
        h5 {{ product.name }}
</template>

<script>
import {filter} from 'lodash-es'
import {forEach} from 'lodash-es'


export default {
  props: [ 'product', 'role' ],
  computed: {
    url () {
      return '/orders/create/';
    }
  },

  methods: {
    resolveImage () {
      return this.product.master.image_urls.length
      ? this.product.master.image_urls[0]
      : '/images/product-placeholder.jpg'
    },
    resolveProductPage () {
      this.$router.push(this.url + this.product.id);
    },
    classify () {
      let classname = ''
      forEach(this.product.categories, (c) => {
        classname += ' ' + c.name.replace(/ /ig, '-').replace(/&/ig, '-').toLowerCase()
      })
      return classname
    },
    providers () {
      return filter(this.product.tags, (t) => {
        return t.name.indexOf('_provider:') === 0
      })
    },
    resolve (tag) {
      const provider = tag.replace(/_provider:/i, '')
      return `/images/providers/${provider}.jpg`
    }
  }
}
</script>

