import Validation from "./validation";
import {RequiredValidation} from "./required-validation";
import {PasswordValidation} from "./password-validation";
import {NumberOnlyValidation} from "./number-only-validation";
import {TextOnlyValidation} from "./text-only-validation";
import {AlphaNumericValidation} from "./alpha-numeric-validation";
import {EmailValidation} from "./email-validation";

const commonValidations = new Map([
  ['required', new RequiredValidation()],
  ['textOnly', new TextOnlyValidation()],
  ['password', new PasswordValidation()],
  ['numberOnly', new NumberOnlyValidation()],
  ['email', new EmailValidation()],
  ['alphaNumeric', new AlphaNumericValidation()]
]);

export class CommonValidation extends Validation {
  constructor() {
    super();
    this.validations = commonValidations;
  }

  async validate(leftOperand, rightOperand) {
    const validation = this.validations.get(this.config.commonValidationName);
    const result = validation.validate(leftOperand, rightOperand);
    return this.yield(result);
  }
}
