<template>
  <div class="padding">
    <p>{{currentField.displayName}}
    <dxcTooltip :tooltiptext="currentField.description"></dxcTooltip>
    </p>
     <div v-if="isAutoSuggestionMulti">
      <multiselect
        v-model="value"
        :tag-placeholder="currentField.currentValue"
        :options="suggestions"
        :close-on-select="false"
        :searchable = "true"
        :allow-empty="false"
        :multiple="true"
        track-by="id" label="name"
        :name="currentField.name"
        @search-change="getAutoSuggestions"
      ></multiselect>
    </div>
    <div v-else-if="isAutoSuggestion">
      <autocomplete
        :placeholder="currentField.currentValue"
        @selected="updateSelectedSuggestion"
        :source="async function(input) {
          clearSelection();
          return getAutoSuggestions(input)
        }"
        :results-value="currentField.validations[0].attributeName"
        results-property="data"
        :results-display="currentField.validations[0].attributeName"
        input-class="field-width">
      </autocomplete>
    </div>
    <div v-else-if="enable_group_order && group_order_field_id===currentField.id && isGroupOrder">
      <multiselect
        v-model="value"
        tag-placeholder="Select Group"
        :options="groups"
        :close-on-select="false"
        :searchable = "true"
        :allow-empty="false"
        :multiple="false"
        track-by="id" label="name"
        :name="currentField.name"
        @input="updateGroupOrder"
      ></multiselect>
    </div>
    <div v-else>
      <label>
        <input
          type="text"
          :placeholder="currentField.placeholder"
          :value="currentField.currentValue"
          :name="currentField.name"
          @change="update"
          class="field-width"
          @blur="validate">
      </label>
      <p class="error-text"> {{ errors }}</p>
    </div>

    <div v-if="enable_group_order && group_order_field_id===currentField.id">
        <input type="checkbox" id="groupOrder" v-model="isGroupOrder" @change="toggleGroupOrder">
        <label>Group Order</label>
    </div>
  </div>
</template>

<script lang="js">
  import Vue from 'vue'
  import dynamicFormMixin from "../../dynamic-form-mixin";
  import DxcTooltip from '../dxc-tooltip/index'
  import {Multiselect} from 'vue-multiselect';


  export default {
    components:{
        dxcTooltip: DxcTooltip,
        Multiselect

      },
      data() {
        return {
          isGroupOrder: false,
          value: null
        }
      },
    mixins: [dynamicFormMixin],
    beforeDestroy() {
      this.$store.dispatch('orders/setGroupOrders', {})
    },
    methods: {
      async toggleGroupOrder(){
        if(this.isGroupOrder){
          await this.$store.dispatch('machine-groups/getAllGroups')
        }else{
          await this.$store.dispatch('orders/setGroupOrders', {})
        }

      },

      async updateGroupOrder(){
        if(this.isGroupOrder){
          await this.$store.dispatch('orders/setGroupOrders', {name:this.currentField.name, value: this.value})
        }
      }
    },
  }
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style lang="css">
  .multiselect__spinner:before {
    border-top-color: #00afe1
  }
  .multiselect__tag {
    background: #00afe1
  }
  .multiselect__option--highlight {
    background: #00afe1
  }
  .multiselect__option--highlight:after {
    background: #00afe1
  }
</style>
<style lang="sass" scoped>
  @import '../../../assets/styles/includes'
  .padding
    padding: 0 1.5rem 1.5rem

  .field-width
    width: 25rem

  .error-text
    font-size: 0.7rem
    color: red

  .autocomplete
    position: relative
    width: 25rem

</style>
