<template lang="pug">
.products
  h1.title.h2(v-if="title") {{ showing }}
  .grid(v-if="products && products.length")
    .grid-cell(v-for="product in products")
      product-summary(v-if="product" :key="product.id", :product="product", :role="role")
  p.no-data(v-else-if="loadingProducts")
    | Searching Products ...
  p.no-data(v-else)
    | No Products Found !
</template>

<script>
import ProductSummary from '@/components/product/ProductSummary'
import { mapGetters, mapActions } from 'vuex'

export default {
  props: [ 'products', 'title', 'role', 'loadingProducts' ],
  components: {
    ProductSummary
  },
  computed: {
    showing () {
      return this.title || 'Recommended Products'
    }
  },
}
</script>

