import { ActiveDirectoryValidation } from './active-directory-validation'
import { CommonValidation } from './common-validation'

export default class OfflineValidationFactory {
  static getOfflineValidation(name) {
    const validations = {
      'activeDirectoryValidation': new ActiveDirectoryValidation(),
      'commonValidation': new CommonValidation()
    };
    return validations[name];
  }
}
