    import Router from 'vue-router'
    import Vue from 'vue'
    import Home from '../views/home/index.vue'
    import error from '../views/error/401.vue'
    import Create_Order from '../views/orders/create/index.vue'
  
    
    const routes = [
      {
        path: '/',
        redirect:'/error',
      },
      {
         path:'/home',
         name:'home',
         component: Home,

      },
      {
        path: '/orders/create/:id',
        name: 'create_order',
        component: () => Create_Order,

      },
      {
        path:'/error',
        name:'error',
        component: error,

      }
  
    ]
    Vue.use(Router);

    const router = new Router({mode : 'history',routes});


    export default router;
