<template>
    <span v-if ="(null!=this.tooltiptext) && (this.tooltiptext.length >0)">
        <button v-tooltip.top= "this.tooltiptext" style="padding:0;border: none;outline:none;">              
            <span class="fa fa-sm fa-question-circle"/>
        </button>    
    </span>         
</template>

<script>
    import Vue from 'vue'
    import VTooltip from 'v-tooltip';

    Vue.use(VTooltip)

    VTooltip.options.autoHide=false
    VTooltip.options.defaultTrigger='hover'
    VTooltip.options.defaultClass ='my-tooltip'
    VTooltip.options.delay={show:'500',hide:'100'}
    export default {
        props:['tooltiptext']
    }

</script>

