const CONSTANTS = {
  UNRESTRICTED_PATHS : ['/', '/password-reset/', '/sso-failure/', '/signup/', '/legal/terms-of-use/', '/legal/privacy-policy/', '/customer-registration/']
}
const getRoutePath = (route) => route.endsWith('/') ? route : `${route}/`

const wrapWithLoader = async function (executableFunction) {
  await this.$store.dispatch('loader/setLoading')
  await executableFunction.call(this)
  await this.$store.dispatch('loader/clearLoading')
}

const fireIfLatest = async function (executableFunction, response) {
  const {startTime} = response.config.metadata;
  const previousSearchedTime = this.getters.lastSearchedTime
  if(startTime > previousSearchedTime) {
    executableFunction();
    this.commit('setLastSearchedTime');
  }
}

const sortByProperty = function (property) {
  return function(a,b){
    if(a[property] > b[property])
      return 1;
    else if(a[property] < b[property])
      return -1;

    return 0;
  }
}

export { CONSTANTS, getRoutePath, wrapWithLoader, fireIfLatest, sortByProperty }
