<template lang="pug">
  <div>
    <div class="padding">
      <p>Enter details to submit your order</p>
    </div>
    <div v-for="field of schema" :key="field.id" >
      <component v-if="canRender(field)" :is="field.fieldType" :id="field.id" :reValidate="triggerValidation" :orderDetails="getOrderDetails()"></component>
    </div>
    <div class="padding-schedule" v-if="publishedProduct.is_scheduled">
      <div style="padding-bottom:24px;" v-if="!editOrder">
        <a @click="showRadioButton" style="text-decoration:underline; cursor:pointer; margin-right:1rem">Schedule Order</a>
        <a @click="showMultiSelectDropDown" style="text-decoration:underline; cursor:pointer;">Group Order</a>
      </div>
      <div v-show="radioButtonVisible" style="padding-bottom:24px;">
        <input type="radio" name="schedule" value="onetime" v-model="schedule_type" style="margin-left:0px;" @change="resetSummary">
        <label> One-Time </label>
        <input type="radio" name="schedule" value="recurr" v-model="schedule_type" @change="resetSummary">
        <label> Recurring </label>
      </div>
      <div v-show="schedule_type==='onetime' || schedule_type==='recurr'" style="padding-bottom:24px;">
        <p style="margin-bottom:0px">Scheduled Date/Time (time in UTC)</p>
        <p style="margin-top:0px; font-size:90%; margin-bottom:16px; color:#6d706e;">Min Value: {{timeAfterOneHour.slice(4)}}, Max Value: {{timeAfterOneYear.slice(4)}} </p>
        <input type="datetime-local" v-model="scheduledDate" @change="updateScheduledDate" @invalid="invalidDate" v-if="!editOrder"  min="1000-01-01T00:00" max="9999-01-01T00:00">
        <h3 v-if="editOrder">{{scheduledDate.split("T").join(" ")}}</h3>
        <span class="validity"></span>
        span.error(v-show="error.length>0")
          | {{error}}
      </div>

      <div v-show="schedule_type==='recurr'">
        <div class="end" style="padding-bottom:24px;">
          <p style="margin-bottom:16px;">End Date & Time</p>
            <input type="datetime-local" v-model="to" @change="updateEndDate" @invalid="invalidDate"  min="1000-01-01T00:00" max="9999-01-01T00:00">
            <span class="validity"></span>
            span.error(v-show="end_error.length>0")
              | {{end_error}}
        </div>
        <div class="dropdown" style="padding-bottom:24px;">
          label(style="padding-right:5px; font-size:1rem;") Recurrence
          select(v-model="recurrence_info.recurrence_type_id" @change="onRecurrenceTypeChange()" style="padding:0 1.5rem 1.5rem 1.5rem;")
            option(disabled, value=0) Select
            option(v-for="type in recurrence_type" :value="type.id") {{type.name}}
        </div>
        <div v-show="recurrence_info.recurrence_type_id!=0 && recurrence_info.recurrence_type_id=== 1 ">
          label(style="padding-right:25px; font-size:1rem;") Every
          select(v-model="recurrence_info.separation_count" style="padding-left:25px; padding-right:25px; margin-bottom:16px;" @change="onHourChange")
            option(disabled, value=0 ) Select
            option( v-for="index in 24" :value="index") {{index}}
          label(style="padding-left:25px; font-size:1rem;") Hour/s
        </div>
        <div v-show="recurrence_info.recurrence_type_id!=0 && recurrence_info.recurrence_type_id===2">
          label(style="padding-right:25px; font-size:1rem;") Every
          select(style="padding-left:25px; padding-right:25px; margin-bottom:16px;" v-model="recurrence_info.separation_count" @change="onDayChange")
            option(disabled, value=0) Select
            option( v-for="index in 15" :value="index") {{index}}
          label(style="padding-left:15px; font-size:1rem;") Day/s
        </div>
        <div v-show="recurrence_info.recurrence_type_id!=0 && recurrence_info.recurrence_type_id===3">
          <div>
            label(style="padding-right:25px; font-size:1rem;") Every
            select(style="padding-left:25px; padding-right:25px; margin-bottom:16px;" v-model="recurrence_info.separation_count" @change="weekchange")
              option(disabled, value=0) Select
              option( v-for="index in 4" :value="index") {{index}}
            label(style="padding-left:15px; font-size:1rem;") Week/s
          </div>
          <div>
            <span v-for="(w, index) in week" style="padding-left:5px;">
              <input  type="checkbox" id="w" name="selector" :value="index+1" v-model="recurrence_info.days_of_week" @change="weekdayChange">
              <label  class="selector" :for="w" style="text-transform:capitalize; font-size:1rem;">  {{w.substring(0,3)}}</label>
            </span>
          </div>
        </div>
        <div v-show="recurrence_info.recurrence_type_id!=0 && recurrence_info.recurrence_type_id===4">
          <div style="padding-bottom:24px;">
            label(style="font-size:1rem;") Every
            select(style="padding-left:50px; padding-right:50px;" v-model="recurrence_info.separation_count" @change="monthChange")
              option(disabled, value=0) Select
              option( v-for="index in 6" :value="index") {{index}}
            label(style="font-size:1rem;") Month/s
          </div>

            <div style="float:left;">
              label(style="font-size:1rem;") Day
              select(style="padding-left:25px; padding-right:25px;" @change="monthDayChange" v-model="recurrence_info.week_of_month")
                option(disabled, :value="null") Select
                option( v-for="(day,index) in day_step" :value="index+1") {{day}}
                option(:value="-1") Last
              select(style="padding-left:25px; padding-right:25px;" @change="monthWeekChange" v-model="week_in_month" )
                option(disabled, :value="null") Select
                option(v-for="(w,index) in week" :value="index" style="text-transform:capitalize;") {{w}}

            </div>
            <div style="float:left;">
              label(style="font-size:1rem;") <span style="padding-left:10px; padding-right:10px; font-weight:bold;">OR</span> Date
              select(style="padding-left:25px; padding-right:25px;" @change="dateChange" v-model="recurrence_info.day_of_month")
                option(disabled, :value="null") Select
                option(v-for="index in 30" :value="index") {{index}}
                option(:value="-1") Last Day
            </div>

        </div>
      </div>
      span.error(v-show="modal_error.length>0")
        | {{modal_error}}
      <div class="flex-container">
        <div class="padding pull-left flex-child" style="padding-left:0px; padding-top:24px;" v-show="schedule_type==='recurr'">
          <a @click="showModal" style="text-decoration:underline; cursor:pointer;"> View Schedule </a>
        </div>
        <div class="padding pull-left flex-child" style="padding-left:0px; padding-top:24px;" v-if="!editOrder" v-show="schedule_type==='onetime' || schedule_type==='recurr'">
          <a @click="cancel_schedule" style="text-decoration:underline; cursor:pointer;"> Cancel Schedule </a>
        </div>
      </div>
    </div>

    <div class="padding pull-right">
      <button class="btn btn-primary" :disabled="isOrderBtnDisabled || disableOrderButton" @click="order" >{{getButton}}</button>
    </div>
    .modal(v-if="isModalVisible",backdrop:false)
        .mask(@click="hide")
        .dialog
          .container
            .tabs
              .tab-content
                main.content(style="height:22rem;")
                  <div class="summary" style="padding-top:2.5rem" v-show="showSummary()">
                    <div class="box" style="width:25rem; padding-top:0.5rem">
                      <p style="margin:0px; font-weight:bold;">{{summary_display.recurr_type}} Schedule</p>
                      <div class="flex-container">
                        <p style="margin:0px; display:inline; margin-right:0.3rem; padding-right: 1rem;" class="flex-child"><span style="font-weight:bold;">From:</span> {{summary_display.from.split("T").join("\xa0\xa0\xa0")}}</p>
                        <p style="margin:0px; display:inline;" class="flex-child"><span style="font-weight:bold;">To:</span>    {{summary_display.to.split("T").join("\xa0\xa0\xa0")}}</p>
                      </div>
                      <br>
                      <p style="margin:0px; font-weight:bold;">Every {{this.summary_display.recurr_details}}</p>
                    </div>
                    <br>
                    <div class="box">
                      <div v-if="occurances.length===0">
                        <p style="margin:0px; font-weight:bold;"> No Next Occurrences</p>
                      </div>
                      <div v-else>
                        <p style="margin:0px; font-weight:bold;"> Next Occurrences</p>
                        <p style="margin:0px" v-for="time in occurances">{{time.split("T").join("\xa0\xa0\xa0").slice(0,-13)}}</p>
                      </div>
                    </div>

                  </div>


  </div>
</template>

<script lang="js">
import {cloneDeep,compact,chain} from "lodash-es";

  import { mapGetters, mapState } from 'vuex';
  import TextInput from './components/text-input/index'
  import PasswordInput from './components/password-input/index'
  import DateInput from './components/date-input/index'
  import TextAreaInput from './components/text-area-input/index'
  import SelectInput from './components/select-input/index'
  import RadioInput from './components/radio-input/index'
  import constants from './dynamic-form-constants';

  export default {
    props: [ 'disableOrderButton',"editOrder"],
    components: {
      'textInput': TextInput,
      'passwordInput': PasswordInput,
      'dateInput': DateInput,
     'textAreaInput': TextAreaInput,
      'selectInput': SelectInput,
      'radioInput': RadioInput
    },
    data() {
      return {
        triggerValidation: false,
        isOrderBtnDisabled: false,
        isModalVisible: false,
        radioButtonVisible: false,
        occurenceType:["Hour","Day","Week","Month"],
        week: ["Monday","Tuesday","Wednesday","Thursday","Friday","Saturday","Sunday"],
        day_step: ["First","Second" ,"Third","Fourth"],
        error: '',
        end_error: '',
        from : '',
        schedule_type: this.editOrder? "recurr" : '',
        scheduledDate: '',
        to : '',
        modal_error:'',
        recurrence_info :  {
          recurrence_type_id : 0,
          separation_count : 0,
          days_of_week : [],
          week_of_month : null,
          day_of_month : null
        },
        recurrence_info_save : {},
        summary_display : {
          recurr_type: '',
          from : '',
          to : '',
          recurr_details : ''
        },
        week_in_month:null,
        timeAfterOneHour: '',
        timeAfterOneYear: '',
        change: false
      }
    },
    watch:{
      modal_error: function(){
        if(this.modal_error===''){
          this.isOrderBtnDisabled = false;
        }else{
          this.isOrderBtnDisabled = true;
        }
      },
    },
    computed: {
      ...mapGetters({
        schema: 'dynamic_form/schema',
        orderDetails: 'orders/orderDetails',
        publishedProduct: 'products/publishedProduct',
        recurrence_type: 'dynamic_form/recurrence_type',
        occurances : 'orders/occurances'

      }),
      ...mapState({
        variantId: state => state.products.product.master.id
      }),
      schemaErrors() {
        return chain(this.schema)
          .map(field => field.errors)
          .flatten()
          .value();
      },
      getButton(){
        if(this.editOrder) return "Update"
        return "Order"
      },

    },
    created() {
      let temp_schema = cloneDeep(this.publishedProduct.schema)
      this.$store.dispatch('dynamic_form/setSchema', temp_schema).then(() => {
        this.setCurrentValueForProductOptionsWithOneOption(this.schema);
      });
      this.$store.dispatch('dynamic_form/fetchReccurence_type');
      if(this.editOrder){
        this.scheduledDate = this.orderDetails["recurrence_info"]["master_order"]["start_date_time"].slice(0,-8)
        this.to = this.orderDetails["recurrence_info"]["master_order"]["end_date_time"].slice(0,-8)
        this.recurrence_info["recurrence_type_id"] = this.orderDetails["recurrence_info"]["order_schedule"]["dxc_recurrence_type_id"]
        this.recurrence_info["separation_count"] = this.orderDetails["recurrence_info"]["order_schedule"]["separation_count"]
        this.recurrence_info["days_of_week"] =  this.orderDetails["recurrence_info"]["order_schedule"]["days_of_week"]?
            this.orderDetails["recurrence_info"]["order_schedule"]["days_of_week"]: []
        this.recurrence_info["week_of_month"] = this.orderDetails["recurrence_info"]["order_schedule"]["week_of_month"]
        this.recurrence_info["day_of_month"] = this.orderDetails["recurrence_info"]["order_schedule"]["day_of_month"]
        // this.recurrence_info_save = this.recurrence_info
        var now_utc = new Date(new Date().toUTCString().substr(0, 25))
        this.timeAfterOneHour = (new Date(+now_utc + (1.8e6*2))).toString().slice(0,-34);
        this.timeAfterOneYear = (new Date(now_utc.setFullYear(now_utc.getUTCFullYear() + 1))).toString().slice(0,-34);
        this.summary_display.recurr_type = this.occurenceType[this.recurrence_info["recurrence_type_id"]-1]
      }
    },

    methods: {

      updateScheduledDate(event) {
        this.modal_error=''
        var currentTime = new Date()
        var now_utc = new Date(new Date().toUTCString().substr(0, 25))
        var timeAfterHalfHour = new Date(+now_utc + 1.8e6)
        var scheduledDateConverted = new Date(this.scheduledDate)
        var oneYearFromNow = new Date(now_utc.setFullYear(now_utc.getUTCFullYear() + 1))
        this.error=''
        this.isOrderBtnDisabled = false;
        if (scheduledDateConverted <= timeAfterHalfHour){
          this.error='Order can be scheduled 1 hour from now'
          this.isOrderBtnDisabled = true;
        }
        if(scheduledDateConverted > oneYearFromNow){
          this.error='Order can only be sceduled in less than a Year'
          this.isOrderBtnDisabled = true;
        }
        else{
          this.$store.dispatch('orders/updateScheduledStartDate', this.scheduledDate)
        }
      },

      updateEndDate(){
        this.modal_error=''
        this.end_error=''
        this.isOrderBtnDisabled = false;
        var endDateConverted = new Date(this.to)
        var now_utc = new Date(new Date().toUTCString().substr(0, 25))
        var oneYearFromNow = new Date(now_utc.setFullYear(now_utc.getUTCFullYear() + 1))
        if(endDateConverted > oneYearFromNow){
          this.end_error='Order can only be sceduled in less than a Year'
          this.isOrderBtnDisabled = true;
        }
        this.change=true
      },
      compareDate(date1, date2){
        if(date1 > date2){
          this.isOrderBtnDisabled = true;
          this.modal_error = 'Start date can not be greater than end date '
        }
      },
      invalidDate(){
        this.error='Invalid Date'
        this.isOrderBtnDisabled = true;
      },
      setCurrentValueForProductOptionsWithOneOption(schema) {
        schema.forEach(field => {
          if (this.hasOnlyOneOption(field)) {
            const attributeInfo = {
              id: field.id,
              name: constants.CURRENT_VALUE,
              value: field.options[0].value
            };
            let input = {};
            input[field.name] = field.options[0].value;
            this.$store.dispatch('orders/updateSelectedOptions', input);
            this.$store.dispatch('dynamic_form/updateFieldAttribute', attributeInfo);
          }
        });
      },
      createOrder() {
        let that = this;
        var params = {
          variantId : this.variantId,
          recurrence_info_save : this.recurrence_info_save,
          scheduled_end_date_time : this.to,
          scheduled_start_date_time : this.scheduledDate
        }
        this.$store.dispatch('orders/createOrder', params)
          .then(function () {
            that.$router.push({path: "/orders"})
          })
          .catch();
      },

      updateOrder() {
        let that = this;
        var params = {
          variantId : this.variantId,
          recurrence_info_save : this.recurrence_info_save,
          scheduled_end_date_time : this.to,
          scheduled_start_date_time : this.scheduledDate,
          order_id: this.orderDetails.id,
        }
        this.$store.dispatch('orders/editOrder', params)
          .then(function () {
            that.$router.push({path: "/orders"})
          })
          .catch();
      },
      isPreFilledInput(field) {
        return constants.PRE_FILLED_INPUT_TYPES.includes(field.fieldType);
      },
      hasOnlyOneOption(field) {
        return field && field.options && field.options.length === 1
      },
      canRender(field) {
        return !this.isPreFilledInput(field) && !this.hasOnlyOneOption(field);
      },
      getOrderDetails(){
        if(this.editOrder) return this.orderDetails
        return false
      },

      order() {
        this.isOrderBtnDisabled = true;
        this.triggerValidation = true;
        if(this.schedule_type==='onetime'){
          this.saveDateOneTime()
        }else if(this.schedule_type==='recurr'){
          this.saveDataRecurr();
          if(this.scheduledDate !='' && this.to!=''){
            this.compareDate(this.scheduledDate, this.to);
          }
        }
        if (this.modal_error!=''){
          return
        }else{
          if(!this.editOrder){
            setTimeout(() => {
              if(this.schedule_type==='recurr'){
                if(compact(this.occurances).length <= 0){
                  this.modal_error = 'No recurrence for this duration'
                  return;
                }else{
                  this.modal_error=''
                  if (compact(this.schemaErrors).length <= 0) {
                    this.createOrder();
                    return
                  }
                  return
                }
              }else{
                  if (compact(this.schemaErrors).length <= 0) {
                  this.createOrder();
                }
              }

            }, 2000);
          }else{
            setTimeout(() => {
              if(this.schedule_type==='recurr'){
                if(compact(this.occurances).length <= 0){
                  this.modal_error = 'No recurrence for this duration'
                  return;
                }else{
                  this.modal_error=''
                  if (compact(this.schemaErrors).length <= 0) {
                    this.updateOrder();
                    return
                  }
                  return
                }
              }else{
                  if (compact(this.schemaErrors).length <= 0) {
                  this.updateOrder();
                }
              }

            }, 2000);
        }

        }
      },
      showModal(){
        this.saveDataRecurr()
        if (this.modal_error!=''){
          return
        }else{
          this.isModalVisible = true
        }

      },
      showRadioButton(){
        this.radioButtonVisible=true
      },
      showMultiSelectDropDown(){

      },
      hide() {
        this.isModalVisible = false;
      },
      resetSummary(){
        var now_utc = new Date(new Date().toUTCString().substr(0, 25))
        this.timeAfterOneHour = (new Date(+now_utc + (1.8e6*2))).toString().slice(0,-34);
        this.timeAfterOneYear = (new Date(now_utc.setFullYear(now_utc.getUTCFullYear() + 1))).toString().slice(0,-34);
        this.modal_error=''
        this.summary_display = {
          recurr_type: '',
          from : '',
          to : '',
          recurr_details : ''
        }
        this.recurrence_info = {
          recurrence_type_id : 0,
          separation_count : 0,
          days_of_week : [],
          week_of_month : null,
          day_of_month : null
        }
        this.week_in_month = null
        this.scheduledDate = ''
        this.to = ''
        this.recurrence_info_save={}
      },
      showSummary(){
        if(this.summary_display.recurr_type!='' &&
          this.summary_display.from!='' &&
          this.summary_display.to!='' &&
          this.summary_display.recurr_details!='' ){
            return true
          }
        else{
          return false
        }

      },
      onRecurrenceTypeChange(){
        this.modal_error=''
        this.recurrence_info.separation_count = 0
        this.recurrence_info.days_of_week=[]
        this.recurrence_info.week_of_month = null
        this.recurrence_info.day_of_month = null
        this.week_in_month = null
        this.summary_display.recurr_type = this.occurenceType[this.recurrence_info.recurrence_type_id - 1];
        this.change=true
      },
      weekchange(){
        if (this.recurrence_info.separation_count!=0){
          this.modal_error=''
        }
        this.change=true
      },
      monthChange(){
        if (this.recurrence_info.separation_count!=0){
          this.modal_error=''
        }
        this.change=true
      },
      weekdayChange(){
        if (this.recurrence_info.days_of_week.length>0){
          this.modal_error=''
        }
        this.change=true
      },
      onDayChange(){
        if(this.recurrence_info.separation_count!=0){
          this.modal_error=''
        }
        this.change=true
      },
      onHourChange(){
        if(this.recurrence_info.separation_count!=0){
          this.modal_error=''
        }
        this.change=true
      },
      dateChange(){
        this.recurrence_info.week_of_month=null
        this.week_in_month=null
        this.recurrence_info.days_of_week=[]
        this.modal_error=''
        this.change=true
      },
      monthWeekChange(){
        this.recurrence_info.day_of_month = null
        this.recurrence_info.days_of_week=[this.week_in_month+1]
        this.modal_error=''
        this.change=true
      },
      monthDayChange(){
        this.recurrence_info.day_of_month = null
        this.modal_error=''
        this.change=true
      },
      cancel_schedule(){
        this.radioButtonVisible=false;
        this.schedule_type='';
        this.scheduledDate='';
        this.to='';
        this.occurances = []
        this.resetSummary()
      },
      saveDateOneTime(){
        this.modal_error=''
        if (this.scheduledDate===''){
          this.modal_error='Choose Schedule Date'
        }

      },
      getOccurance(){
          var params;
        if (!this.editOrder){
          params = {
                recurrence_info_save : this.recurrence_info_save,
                scheduled_end_date_time : this.to,
                scheduled_start_date_time : this.scheduledDate
              }
          this.$store.dispatch('orders/getOccurance',params)
        }else{
          if(!this.change){
            params = {
                recurrence_info_save : this.recurrence_info_save,
                scheduled_end_date_time : this.to,
                scheduled_start_date_time : this.orderDetails["recurrence_info"]["master_order"]["next_run_time"].slice(0,-8)
              }
              this.$store.dispatch('orders/getOccurance',params)
          }else{
            var now_utc = new Date().toISOString().slice(0,-8)
            params = {
                  recurrence_info_save : this.recurrence_info_save,
                  scheduled_end_date_time : this.to,
                  scheduled_start_date_time : now_utc
                }
            this.$store.dispatch('orders/getOccurance',params)
          }

        }
      },
      saveDataRecurr(){
        this.modal_error=''
        if (this.scheduledDate===''){
          this.modal_error='Choose Schedule Date'
        }
        else if (this.to===''){
          this.modal_error='Choose End Date'
        }

        if(this.scheduledDate !='' && this.to!=''){
          this.compareDate(this.scheduledDate, this.to);
        }
        this.summary_display.from = this.scheduledDate
        this.summary_display.to = this.to

        if (this.recurrence_info.recurrence_type_id===0){
          this.modal_error='Choose Recurrrence Type'
        }
        if(this.summary_display.recurr_type==='Hour'){
          if(this.recurrence_info.separation_count!=0){
            this.summary_display.recurr_details = this.recurrence_info.separation_count+' hours'
            this.recurrence_info_save = this.recurrence_info
            this.getOccurance()
          }
          else{
            this.modal_error='Choose Recurrrence Period'
          }
        }
        else if(this.summary_display.recurr_type==='Day'){
          if(this.recurrence_info.separation_count!=0){
            this.summary_display.recurr_details = this.recurrence_info.separation_count+' days'
            this.recurrence_info_save = this.recurrence_info
            this.getOccurance()

          }else{
            this.modal_error='Choose Recurrrence Period'
          }
        }
        else if(this.summary_display.recurr_type==='Week'){
          if(this.recurrence_info.days_of_week.length>0 && this.recurrence_info.separation_count!=0 ){
            var week_str=''
            for(let i of this.recurrence_info.days_of_week){
              week_str+=(this.week[i-1]).substring(0,3)+', '
            }
            week_str = week_str.slice(0,-2);
            this.summary_display.recurr_details = this.recurrence_info.separation_count+' Week on ' + week_str
            this.recurrence_info_save = this.recurrence_info
            this.getOccurance()
          }else{
            this.modal_error='Choose Recurrrence Period and Day'
          }
        }
        else if(this.summary_display.recurr_type==='Month'){
          if(this.recurrence_info.separation_count!=0 &&
            (this.recurrence_info.day_of_month!=null ||
            (this.recurrence_info.days_of_week.length===1 && this.recurrence_info.week_of_month !=null))){
              if(this.recurrence_info.day_of_month!=null){
                  if(this.recurrence_info.day_of_month===-1){
                    this.summary_display.recurr_details = this.recurrence_info.separation_count+' Month on last day'
                  }else{
                    this.summary_display.recurr_details = this.recurrence_info.separation_count+' Month on '+this.recurrence_info.day_of_month
                  }
              }else{
                var selected_week = this.week[this.recurrence_info.days_of_week[0]-1]
                if (this.recurrence_info.week_of_month===-1){
                  this.summary_display.recurr_details = this.recurrence_info.separation_count+' Month on Last '+selected_week
                }else{
                  var day_step = this.day_step[this.recurrence_info.week_of_month-1]
                  this.summary_display.recurr_details = this.recurrence_info.separation_count+' Month on '+day_step+' '+selected_week
                }
              }
              this.recurrence_info_save = this.recurrence_info
              this.getOccurance()
            }else{
              this.modal_error='Choose All information'
            }
        }
      }
    }
  }
</script>
