import {forEach,concat,isEmpty} from 'lodash-es'
import newCategory from '../static/seed/factory/category'
import { $,$s3,auth_token } from '../plugins/axios'
import Vue from 'vue'

const state = () => ({
  categories: null,
  category: null,
  message: null,
  success: null,
  all_categories: null,
  imageLoading: false,
  imageMessage: null,
  imageSuccess: false,
  sub_categories: null,
  current_category: null,
  all_bread_crumb_links: [],
  current_page_bread_crumb_links: {}
})

const getters = {
  allCategories: state => {
    return state.all_categories
  },
  categories: state => {
    return state.categories
  },
  category: state => {
    return state.category
  },
  success: state => {
    return state.success
  },
  message: state => {
    return state.message
  },
  imageLoading: state => {
    return state.imageLoading
  },
  imageSuccess: state => {
    return state.imageSuccess
  },
  imageMessage: state => {
    return state.imageMessage
  },
  subCategories: state => {
    return state.sub_categories
  },
  currentCategory: state => {
    return state.current_category
  },
  allBreadCrumbLinks: state => {
    return state.all_bread_crumb_links
  },
  currentPageBreadCrumbLinks: state => {
    console.log("currentPageBreadCrumbLinks")
    console.log(state.current_page_bread_crumb_links)
    return state.current_page_bread_crumb_links
  }
}

const findInCategories = (parent, children, category) => {
  let result = null
  forEach(children, (c, i) => {
    if (c.dxc_tenant_id === category.id) {
      result = { parent, category: c, i}
    } else if (c && c.children) {
      result = findInCategories(c, c.children, category)
    }
  })
  return result
}
const addChildrenIntoCategories = (categories, children,category)=>{
  let result = categories
  forEach(result, (c,i)=>{
    if(c.id === category.id){
        result[i]['children'] = children
        return result
    }else if(c && c.children){
      result[i]['children'] = addChildrenIntoCategories(c.children,children,category)
    }
  })
  return result
}

const mutations = {
  setCategory(state,cat){
    state.category = cat
  },
  getAllCategories(state, categories) {
    state.all_categories = categories
  },
  updateCategoryDetails (state, category) {
    let r = findInCategories(state, state.all_categories, category)
    if (r && r.parent['all_categories']) {
      Vue.set(state.all_categories, r.i, category)
    } else if (r) {
      Vue.set(r.parent, r.i, category)
    }
  },
  getCategories(state, categories) {
    state.categories = categories
  },
  getSubCategories(state, subcategories) {
    state.subcategories = subcategories
  },
  resetCategory(state){
    state.category = null
  },
  async getCategory(state, category) {
    let categories  = addChildrenIntoCategories(state.categories,category.children, category)
    // let c = await findInCategories(state.categories, category.children, category)
    // if (c) Vue.set(c, 'children', category.children)
    state.categories = categories
    // Vue.set(state, 'categories', categories);
    state.category = category
  },
  updateCategory: (state, category) => {
    state.category[category.key] = category.value
  },
  addCategory: (state, category) => {
    state.category = category
  },
  flashMessage: (state, flash) => {
    state.message = flash.message
    state.success = flash.success
  },
  clearMessage: (state) => {
    state.message = ''
    state.success = false
  },
  imageLoading: (state, flag) => {
    state.imageLoading = flag
  },
  flashImageMessage: (state, flash) => {
    state.imageMessage = flash.message
    state.imageSuccess = flash.success
  },
  clearImageMessage: (state) => {
    state.imageMessage = ''
    state.imageSuccess = false
  },
  setCurrentCategory(state, currentCategory) {
    state.current_category = currentCategory
  },
  resetCurrentCategory(state){
    state.current_category = null
  },
  resetSubCategories(state){
    state.sub_categories = null
  },
  setSubCategories: (state, subCategories) => {
    state.sub_categories = subCategories
  },
  setAllBreadCrumbLinks: (state, allBreadCrumbLinks) => {
    state.all_bread_crumb_links = allBreadCrumbLinks
  },
  setCurrentPageBreadCrumbLinks: (state, categoryId) => {
    const filteredBreadCrumbs = state.all_bread_crumb_links.filter(breadCrumb => breadCrumb.id == categoryId)[0]
    state.current_page_bread_crumb_links = filteredBreadCrumbs
  }
}

const expand = (parent, children) => {
  parent['children'] = children
  return parent
}

const translate = (category) => {
  return category.parent
  ? expand(category.parent, category.children)
  : expand(category, [])
}

let breadCrumbList = []
const getAllBreadCrumbs = (allCategories) => {
  allCategories.forEach((category_detail) => {
    let parentBreadCrumb = {
      "id": category_detail.parent.id,
      "name": category_detail.parent.name,
      "bread_crumb_list": [
        {
          "label": "Categories Home",
          "path": "/categories-home"
        },
        {
          "label": category_detail.parent.name,
          "path": `/categories-home/category/${category_detail.parent.id}/sub-categories`
        }
      ]
    }
    if (! isEmpty(category_detail.children)) {
      getChildBreadCrumb(parentBreadCrumb, category_detail)
    }
    let isBreadCrumbPresent = breadCrumbList.filter(breadCrumb => breadCrumb.id === parentBreadCrumb.id).length > 0
    if (!isBreadCrumbPresent) {
      breadCrumbList.push(parentBreadCrumb)
    }
  })
  return breadCrumbList
}

const getChildBreadCrumb = (parentBreadCrumb, category_detail) => {
  let childBreadCrumb = {}
  category_detail.children.forEach((childCategory) => {
    childBreadCrumb = {
      "id": childCategory.parent.id,
      "name": childCategory.parent.name
    }
    const childBreadCrumbPath = {
      "label": childCategory.parent.name,
      "path": `/categories-home/category/${childCategory.parent.id}/sub-categories`
    }
    let childBreadCrumbList = concat(parentBreadCrumb.bread_crumb_list, childBreadCrumbPath)
    childBreadCrumb.bread_crumb_list = childBreadCrumbList

    let isBreadCrumbPresent = breadCrumbList.filter(breadCrumb => breadCrumb.id === childBreadCrumb.id).length > 0
    if (!isBreadCrumbPresent) {
      breadCrumbList.push(childBreadCrumb)
    }
    if (! isEmpty(childCategory.children)) {
      getChildBreadCrumb(childBreadCrumb, childCategory)
    }
  })
}

const actions = {
  getNewCategory (context, parent_id) {
    if(parent_id === null){
      context.commit('setCategory',{})
    }else{
      context.commit('getCategory', {})
      context.commit('updateCategory', { key: 'parent_id', value: parent_id})
    }

  },
  updateCategory(context, category) {
    context.commit('updateCategory', category)
  },
  addCategory(context, category) {
    context.commit('addCategory', category)
  },
  resetCategory(context){
    context.commit('resetCategory')
  },
  setSubCategories(context, subCategories) {
    context.commit('setSubCategories', subCategories)
  },
  setCurrentCategory(context, currentCategory) {
    context.commit('setCurrentCategory', currentCategory)
  },
  resetSubCategories(context){
    context.commit('resetSubCategories')
  },
  resetCurrentCategory(context){
    context.commit('resetCurrentCategory')
  },

  async getCategoryById(context, categoryId) {
    return $().get(`categories/${categoryId}`)
      .then((res) => {
        context.commit('setCurrentCategory', res.data.parent)
        context.commit('setSubCategories', res.data.children)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  async getAllCategories(context) {
    return $().get(`categories/all`)
      .then((res) => {
        context.commit('getAllCategories', res.data)
        breadCrumbList = getAllBreadCrumbs(res.data)
        context.commit('setAllBreadCrumbLinks', breadCrumbList)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  async getCategories (context) {
    return $().get(`categories`)
      .then((res) => {
        context.commit('getCategories', res.data)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  async getCategory(context, category) {
    return $().get(`categories/${category.id}`)
      .then((res) => {
        context.commit('getCategory', translate(res.data), category)
      })
      .catch((e) => {
        console.log(e)
      })
  },
  async postCategory(context, category) {
    $()({ method: 'post', url: `categories`, data: category })
      .then((res) => {
        const flash = { success: true, message: 'Category updated!' }
        context.dispatch('getAllCategories');
        context.commit('flashMessage', flash);
        context.dispatch('resetCategory',null);
        context.commit('setCategory',res.data)
        setTimeout(function () {
          context.commit('clearMessage')
        }, 2000)
      })
      .catch(function (error) {
        const flash = { success: false, message: error.response.data.messages.join(', ') }
        context.commit('flashMessage', flash)
        setTimeout(function () {
          context.commit('clearMessage')
        }, 2000)
      })
  },
  async putCategory(context, category) {
    if(category.id) {
      context.commit('clearMessage')
      $()({ method: 'put', url: `categories/${category.id}`, data: category })
        .then((res) => {
          context.dispatch('getAllCategories');
          context.dispatch('getCategories');
          const flash = { success: true, message: 'Category updated!' }
          context.commit('flashMessage', flash)
          context.dispatch('resetCategory',null);
          context.commit('setCategory',res.data)
          setTimeout(function () {
            context.commit('clearMessage')
          }, 2000)
        })
        .catch(function (error) {
          const flash = { success: false, message: error.response.data.messages.join(', ') }
          context.commit('flashMessage', flash)
          setTimeout(function () {
            context.commit('clearMessage')
          }, 2000)
        })
    } else {
      context.dispatch('postCategory', category)
    }
  },
  async deleteCategory(context, category) {
    if(category.id) {
      context.commit('clearMessage')
      $()({ method: 'delete', url: `categories/${category.id}`})
        .then((res) => {
          context.dispatch('getAllCategories');
          const flash = { success: true, message: 'Category deleted!' }
          context.commit('flashMessage', flash)
          setTimeout(function () {
            context.commit('clearMessage')
            context.dispatch('getNewCategory',null)
          }, 2000)
        })
        .catch(function (error) {
          const flash = { success: false, message: "Error: Can Not Delete Master Categories" }
          context.commit('flashMessage', flash)
          setTimeout(function () {
            context.commit('clearMessage')
          }, 2000)
        })
    }
  },

  uploadLogo(context, image) {
    if (image.category && image.blob) {
      context.dispatch('postImage', image)
    }
  },

  async postImage(context, image) {
    context.commit('imageLoading', true)
    $()({ method: 'post', url: `categories/${image.category.id}/image`, data: { alt_text: image.alt } })
      .then(async (res) => {
        const id = res.data.id
        const s3 = res.data.url
        await $s3({ method: 'put', url: s3, data: image.blob, headers: { "Content-Type": "image/jpeg" } })
        .then(async (res) => {
            $().defaults.headers['Authorization'] = `Bearer ${auth_token}`;
            await $()({ method: 'put', url: `categories/${image.category.id}/image/${id}`, data: { uploaded: true } })
              .then((res) => {
                context.commit('imageLoading', false)
                context.dispatch('categories/getCategory', image.category, { root: true })
                context.commit('flashImageMessage', { message: 'image uploaded!', success: true })
                setTimeout(function () {
                  context.commit('clearImageMessage')
                }, 2000)
              })
          });
      })
      .catch(function (error) {
        console.log(error, '$images')
      })
  },
  setCurrentPageBreadCrumbs(context, categoryId) {
    context.commit('setCurrentPageBreadCrumbLinks', categoryId)
  }
}

export default { namespaced: true,state, getters, mutations, actions }
